@charset "UTF-8";
@import url(https://fonts.googleapis.com/css?family=Inconsolata);
@import url(https://fonts.googleapis.com/css?family=PT+Mono);

%counter-margin {
    margin-top: -30px;
    margin-left: -30px;
    margin-right: -30px;
}

%text-alignment {
    [data-text-align="right"] & {
        text-align: right;
    }
    [data-text-align="left--right"] & {
        text-align: left;
        @media (min-width: 768px) {
            text-align: right;
        }
    }
    [data-text-align="sm-md--center"] & {
        text-align: center;
        @media (min-width: 992px) {
            text-align: left;
        }
    }
}

:root {
    --primary: #252527;
    --secondary: #444a;

    --background-color: var(--primary);
    --theme: dark;

    --title-color: #fffa;
    --headline-color: #252527;
    --text--color: #4c4c4c;
    --link--color: #062F4F;
}

body {
    font-family: "Roboto", "Lato", "Helvetica Neue", Helvetica, Arial, serif;
}

.content {
    img,
    embed,
    object,
    video {
        max-width: 100%;
        height: auto;
    }
    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    dt,
    th {
        color: var(--pfe-broadcasted--text, --headline-color);
    }
    p,
    pre,
    li,
    dd,
    td {
        color: var(--pfe-broadcasted--text, --text-color);
    }
    a {
        color: var(--pfe-broadcasted--link, --link--color);
        &:hover {
            color: var(--pfe-broadcasted--link--hover, #002f3b);
        }
        &:focus {
            color: var(--pfe-broadcasted--link--focus, #002f3b);
        }
        &:visited {
            color: var(--pfe-broadcasted--link--visited, #002f3b);
        }
    }
}

.quote-container {
    padding: 0;
    margin: 0;
    list-style: none;
}

.image-embed {
    &-container {
        [data-align='bleed']:first-child & {
            @extend %counter-margin;
        }
        [data-align='bleed'] & {
            margin-left: -30px;
            margin-right: -30px;
        }
    }
    &-img,
    &-svg {
        [data-align='full'] &,
        [data-align='bleed'] & {
            width: 100%;
            height: auto;
            display: block;
        }

    }
}

.slider {
    &[data-align="bleed"] {
        @extend %counter-margin;
    }
}

.video-embed {
    pfe-card &-media-container {
        @extend %counter-margin;
    }
    &-iframe-container {
        padding: 0 0 56.25%;
        height: 0;
        position: relative;
        > * {
            width: 100%;
            height: auto;
            display: block;        
        }
        iframe {
            left: 0;
            top: 0;
            height: 100%;
            width: 100%;
            position: absolute;
        }
    }
}

pfe-band {
    --pfe-band--BackgroundColor: #fff;
    --theme: light;
    &[data-band-background="dark"] {
        --pfe-band--BackgroundColor: var(--primary, #252527);
        --theme: dark;
    }
    &[data-band-background="purple"] {
        --pfe-band--BackgroundColor: rgb(176, 6, 82);
        --theme: saturated;
    }
    &[data-band-background="blue"] {
        --pfe-band--BackgroundColor: #00a7;
        --theme: saturated;
    }
    &[data-band-background="tundora"] {
        --pfe-band--BackgroundColor: var(--secondary, #444a);
        --theme: dark;
    }
    &[data-band-background="opaque"] {
        --pfe-band--BackgroundColor: rgba(37, 37, 39, 0.5);
        --theme: dark;
    }
    &[data-band-background="transparent"] {
        --pfe-band--BackgroundColor: transparent;
    }
}

pfe-card {
    --pfe-card--BackgroundColor: white;
    --theme: light;
    &[data-band-background="dark"] {
        --pfe-card--BackgroundColor: var(--primary, #252527);
        --theme: dark;
    }
    &[data-band-background="tundora"] {
        --pfe-card--BackgroundColor: var(--secondary, #444a);
        --theme: dark;
    }
}

.band-header-title,
.header-title,
.menu-side-link,
.menu-icon-item {
    @extend %text-alignment;
    font-size: 22px;
    margin-bottom: 10px;
    text-transform: uppercase;
    font-weight: 800;
    margin-top: 0;
    line-height: 1.4;
}

.band-header-headline,
.header-headline {
    @extend %text-alignment;
    font-size: 32px;
    margin-bottom: 10px;
    font-weight: 800;
    margin-top: 0;
    line-height: 1.1;
    @media (min-width: 992px) {
        font-size: 39px;
    }
    @media (min-width: 1200px) {
        font-size: 42px;
    }
    a {
        text-decoration: none;
        color: inherit;
        &:hover {
            color: #062F4F;
            [data-theme~="dark"] & {
                color: #fffa;
            }
        }
    }
}

.band-header-summary,
.header-summary {
    @extend %text-alignment;
    font-size: 1.2em;
    line-height: 1.38;
    margin-top: 0;
    a {
        color: #dadada;
        &:hover {
            cursor: pointer;
            color: #0088ce;
        }
        &:visited {
            text-decoration: line-through;
        }
    }
    .extra {
        font-size: 1.4em;
    }
}

