}

.content p:last-child,
.image-embed-caption:last-child,
.video-embed-summary:last-child {
    margin-bottom: 0;
}

.content pre {
    font-family: 'PT Mono', monospace;
    display: block;
    font-size: 1em;
    font-weight: normal;
    line-height: 1.44;
    margin: 0 0 1em;
}

.content pre:last-child {
    margin-bottom: 0;
}

.content a,
.video-embed-summary-link {
    text-decoration: none;
    font-weight: 400;
}

.content a[name]:not([href]),
[name].video-embed-summary-link:not([href]) {
    color: inherit;
}

.content a:hover,
.video-embed-summary-link:hover {
    text-decoration: underline;
}

.content a:visited,
