}

.content dd {
    margin: 0 0 1em;
    font-size: 1em;
    margin-left: 0;
}

.content th {
    font-size: 1em;
    padding: 10px 20px 10px 0;
    border-collapse: collapse;
    border: 0;
}

.content td {
    font-size: 1em;
    padding: 10px 20px 10px 0;
    border-collapse: collapse;
    border: 0;
    border-bottom: 1px solid;
}

.cta-link[data-hover="special"] {
    position: relative;
    display: inline-block;
    outline: none;
    vertical-align: bottom;
    padding: 0 0.25em;
    color: #d04c3f;
    text-decoration: none;
}

.cta-link[data-hover="special"]:hover {
    color: #d04c3faa;
}

.cta-link[data-cta-type="secondary"] {
    font-weight: 800;
    text-decoration: none;
    cursor: pointer;
    padding-right: 15px;
}

.cta-link[data-cta-type="secondary"]:hover {
    text-decoration: none;
}

.cta-link[data-cta-type="secondary"]::after {
    border-style: solid;
    border-width: 7px 6px 0;
    border-color: transparent;
    border-top-color: inherit;
    -webkit-transform: rotate(-90deg);
    transform: rotate(-90deg);
    content: " ";
    position: absolute;
    top: 40%;
    margin-left: .5em;
    line-height: 100%;
    vertical-align: middle;
}

.cta-link[data-cta-type="tertiary"] {
    font-weight: 800;
    text-decoration: none;
    cursor: pointer;
    padding-right: 15px;
}

.cta-link[data-cta-type="tertiary"]:hover {
    text-decoration: none;
}

.cta-link[data-cta-type="tertiary"]::after {
    border-style: solid;
    border-width: 7px 6px 0;
    border-color: transparent;
    border-top-color: inherit;
    -webkit-transform: rotate(-90deg);
    transform: rotate(-90deg);
    content: " ";
    position: absolute;
    top: 40%;
    margin-left: .5em;
    line-height: 100%;
    vertical-align: middle;
}

.cta-link[data-cta-type="button"] {
    background-color: #d04c3f;
    color: white;
    display: inline-block;
    border: 0 solid white;
    font-size: 16px;
    padding: 9px 40px;
    font-weight: bold;
    text-decoration: none;
    text-transform: uppercase;
    -webkit-transition: all .3s ease-in 0s;
    transition: all .3s ease-in 0s;
    cursor: pointer;
}

.cta-link[data-cta-type="button"]:hover {
    background-color: #c13c2f;
    color: white;
    text-decoration: none;
}

.cta-link[data-cta-type="ghost"] {
    background-color: transparent;
    color: white;
    display: inline-block;
    border: 1px solid white;
    font-size: 16px;
    padding: 9px 40px;
    font-weight: bold;
    text-decoration: none;
    text-transform: uppercase;
    -webkit-transition: all .3s ease-in 0s;
    transition: all .3s ease-in 0s;
    cursor: pointer;
}

.cta-link[data-cta-type="ghost"]:hover {
    background-color: white;
    color: #1a1a1a;
    text-decoration: none;
    border-color: #f0f0f0;
}

.cta-link[data-cta-type="disabled"] {
    background-color: #dadada;
    color: #4c4c4c;
    display: inline-block;
    border: 0 solid #4c4c4c;
    font-size: 16px;
    padding: 9px 40px;
    font-weight: bold;
    text-decoration: none;
    text-transform: uppercase;
    -webkit-transition: all .3s ease-in 0s;
    transition: all .3s ease-in 0s;
    cursor: pointer;
}

.cta-link[data-cta-type="disabled"]:hover {
    background-color: #cbcbcb;
    color: #4c4c4c;
    text-decoration: none;
}

[data-cta-type='video-play'] .cta-link {
    --pfe-cta__arrow--Display: none;

    font-size: 1em;
    text-transform: uppercase;
    font-weight: 400;
    text-align: center;
    text-decoration: none;
    color: #252527;
}

[data-cta-type='video-play'] .cta-link::after {
    display: inline-block;
    position: relative;
    content: "\25b6";
    padding: 8px 6px 8px 12px;
    font-size: 25px;
    line-height: 30px;
    border: 2px solid #252527;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    vertical-align: middle;
    margin-left: 15px;
}

[data-theme~="dark"] [data-cta-type='video-play'] .cta-link::after {
    border-color: white;
}

[data-theme~="dark"] [data-cta-type='video-play'] .cta-link {
    color: white;
}

[data-cta-type='video-play'] .cta-link:hover {
    color: #0088ce;
    cursor: pointer;
}

[data-cta-type='video-play'] .cta-link[data-size="sm"]::after {
    padding: 8px 0px 0px 3px;
    font-size: 12px;
    line-height: 10px;
    width: 25px;
    height: 25px;
    border-width: 1px;
}

[data-cta-type='video-play'] .cta-link:hover::after {
    border-color: #0088ce;
}

.quote-quotation {
    font-family: "Inconsolata", "Roboto", "Lato", "Helvetica Neue", Helvetica, Arial, serif;
    margin-top: 0;
    line-height: 1.2;
    font-style: italic;
    font-size: 1.1em;
    margin: 0;
}

@media (min-width: 768px) {
